.dark .dropdown .wrapper{
  background-color:#1A202E;
  background-color:var(--dropdown-bg-color-dark, #1A202E);
  border-color:#364152;
  border-color:var(--dropdown-border-color-dark, #364152)
}
.dark .dropdown .wrapper .body .link{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  border-color:#364152;
  border-color:var(--dropdown-border-color-dark, #364152);
  color:#3182ce;
  color:var(--dropdown-link-color-dark, #3182ce)
}
.dark .dropdown .wrapper .body .link:hover{
  background-color:#27303F;
  background-color:var(--dropdown-link-hover-color-dark, #27303F)
}
.dark .dropdown .wrapper .body .divider{
  border-color:#364152;
  border-color:var(--dropdown-divider-border-color-dark, #364152)
}
.dropdown{
  position:relative;
  display:inline-block;
  text-align:left
}
.dropdown .wrapper{
  background-color:#fff;
  background-color:var(--dropdown-bg-color, #fff);
  border-color:#97A6BA;
  border-color:var(--dropdown-border-color, #97A6BA);
  position:absolute;
  border-radius:0.375rem;
  box-shadow:0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-width:1px;
  z-index:50
}
.dropdown .wrapper.right{
  transform-origin:top left;
  left:0
}
.dropdown .wrapper.left{
  transform-origin:top right;
  right:0
}
.dropdown .wrapper .body{
  overflow:auto
}
.dropdown .wrapper .body .link{
  border-color:#97A6BA;
  border-color:var(--dropdown-border-color, #97A6BA);
  color:#3182ce;
  color:var(--dropdown-link-color, #3182ce);
  display:block;
  padding-left:1.5rem;
  padding-right:1.5rem;
  padding-top:0.75rem;
  padding-bottom:0.75rem;
  line-height:1.25;
  cursor:pointer
}
.dropdown .wrapper .body .link:hover{
  background-color:#F1F5F9;
  background-color:var(--dropdown-link-hover-color, #F1F5F9)
}
.dropdown .wrapper .body .divider{
  border-top-width:1px;
  border-color:#CFD8E3;
  border-color:var(--dropdown-divider-border-color, #CFD8E3)
}