.tooltip[data-v-5ff44e01]{
  position:relative;
  display:block;
  z-index:50;
  transition-property:opacity;
  transition-duration:500ms
}
.tooltip .inner[data-v-5ff44e01]{
  background-color:#1A202E;
  background-color:var(--tooltip-bg-color, #1A202E);
  color:#fff;
  color:var(--tooltip-text-color, #fff);
  padding-top:0.25rem;
  padding-bottom:0.25rem;
  padding-left:0.5rem;
  padding-right:0.5rem;
  border-radius:0.375rem;
  font-size:0.875rem
}
.tooltip .arrow[data-v-5ff44e01]{
  position:absolute;
  border-color:#1A202E;
  border-color:var(--tooltip-arrow-color, #1A202E)
}
.tooltip .arrow.top[data-v-5ff44e01]{
  border-width:5px 5px 0 5px;
  border-left-color:transparent !important;
  border-right-color:transparent !important;
  border-bottom-color:transparent !important;
  bottom:-5px;
  left:calc(50% - 5px);
  margin-top:0;
  margin-bottom:0;
  width:5px
}
.tooltip .arrow.bottom[data-v-5ff44e01]{
  border-width:0 5px 5px 5px;
  border-left-color:transparent !important;
