.dark .card{
  background-color:#27303F;
  background-color:var(--card-bg-color-dark, #27303F);
  color:#CFD8E3;
  color:var(--card-text-color-dark, #CFD8E3)
}
.dark .card.border-default{
  border-color:#364152;
  border-color:var(--card-border-color-dark, #364152)
}
.dark .card .header{
  border-color:#364152;
  border-color:var(--card-border-color-dark, #364152)
}
.dark .card .header h3{
  color:#CFD8E3;
  color:var(--card-title-color-dark, #CFD8E3)
}
.dark .card .footer{
  border-color:#364152;
  border-color:var(--card-border-color-dark, #364152)
}
.card{
  box-shadow:0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border-width:1px;
  border-radius:0.5rem;
  margin-bottom:1rem;
  background-color:#fff;
  background-color:var(--card-bg-color, #fff);
  color:#364152;
  color:var(--card-text-color, #364152)
}
.card.border-default{
  border-color:#97A6BA;
  border-color:var(--card-border-color, #97A6BA)
}
.card .header{
  padding:0.5rem;
  border-color:#97A6BA;
  border-color:var(--card-border-color, #97A6BA)
}
.card .header h3{
  color:#364152;
  color:var(--card-title-color, #364152)
}
.card .footer{
  padding:0.5rem;
  border-color:#364152;
  border-color:var(--card-border-color-dark, #364152)
}