@-webkit-keyframes indeterminate_first{
  0%{
    left:-100%;
    width:100%
  }

  100%{
    left:100%;
    width:10%
  }
}

@keyframes indeterminate_first{
  0%{
    left:-100%;
    width:100%
  }

  100%{
    left:100%;
    width:10%
  }
}

@-webkit-keyframes indeterminate_second{
  0%{
    left:-150%;
    width:100%
  }

  100%{
    left:100%;
    width:10%
  }
}

@keyframes indeterminate_second{
  0%{
    left:-150%;
    width:100%
  }

  100%{
    left:100%;
    width:10%
  }
}

.indeterminate{
  height: 100%;
  width: 100%;
  position: relative
}

.indeterminate .before{
  position: absolute;
  height: 100%;
  content:"";
  -webkit-animation:indeterminate_first 1.5s ease-out infinite;
  animation:indeterminate_first 1.5s ease-out infinite
}

.indeterminate .after{
  position: absolute;
  height: 100%;
  content:"";
  -webkit-animation:indeterminate_second 1.5s ease-in infinite;
  animation:indeterminate_second 1.5s ease-in infinite
}

