.button{
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  padding-top: 0.375rem;
  padding-bottom: 0.375rem;
  border-width: 1px;
  border-color: transparent;
  font-size: 0.875rem;
  line-height: 1rem;
  font-weight: 500;
  border-radius: 0.375rem;
  cursor: pointer;
  background-color:#FBFDFE;
  background-color:var(--button-bg-color, #FBFDFE);
  border-color:#CFD8E3;
  border-color:var(--button-border-color, #CFD8E3);
  color:#475569;
  color:var(--button-text-color, #475569)
}

.button:hover{
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 200ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  background-color:#F1F5F9;
  background-color:var(--button-hover-bg-color, #F1F5F9)
}

.button:focus{
  outline: 0;
  transition-property: background-color, border-color, color, fill, stroke;
  transition-duration: 200ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  background-color:#EAEFF5;
  background-color:var(--button-focus-bg-color, #EAEFF5)
}

